@keyframes zoom-in-out {
  0% {
    width: 45px;
  }
  50% {
    width: 38px;
  }
  100% {
    width: 45px;
  }
}

@keyframes rotate {
  0% {
    transform: rotate(360deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@keyframes pulse-btn {
	0% {
	  transform: scale(1); border-radius: 0;
	}
	70% {
	  transform: scale(1);
	  box-shadow: 0 0 0 20px rgba(254, 84, 2, 0); border-radius: 0;
	}
	  100% {
	  transform: scale(1);
	  box-shadow: 0 0 0 0 rgba(254, 84, 2, 0); border-radius: 0;
	}
  }


